import * as React from 'react';
import Box from '@mui/material/Box';
import CssBaseline from '@mui/material/CssBaseline';
import BottomNavigation from '@mui/material/BottomNavigation';
import BottomNavigationAction from '@mui/material/BottomNavigationAction';
import ShoppingCartIcon from '@mui/icons-material/ShoppingCart';
import VpnKeyIcon from '@mui/icons-material/VpnKey';
import AccountCircleIcon from '@mui/icons-material/AccountCircle';
import Paper from '@mui/material/Paper';
import {sha256} from "js-sha256";

const BotToken = "6443570779:AAECKbblSELnbqtp2tWMNh37Qf9jhwU5Awk"


export default function Home() {
    const [value, setValue] = React.useState(2);
    const ref = React.useRef(null);
    const [Telegram,setTelegram] = React.useState([])
    React.useEffect(() => {
        try {
            const data_check_string = window.Telegram.WebApp.initData
            const hash = window.Telegram.WebApp.initData.split("&hash=")
            const secret_key = sha256.hmac("WebAppData",BotToken)
                if (sha256.hex(sha256.hmac(secret_key,data_check_string)) === hash[1]) {
                    window.alert(window.Telegram.WebApp.initData)
                }else{
                    window.alert("Cant verify!")
                }

        }catch (e) {
            window.alert("Cant read!")
        }
    }, [value]);



    return (
        <Box sx={{ pb: 7 }} ref={ref}>
            <CssBaseline />

            <Paper sx={{ position: 'fixed', bottom: 0, left: 0, right: 0 }} elevation={3}>
                <BottomNavigation
                    showLabels
                    value={value}
                    onChange={(event, newValue) => {
                        setValue(newValue);
                    }}
                >
                    <BottomNavigationAction label=" " icon={<ShoppingCartIcon />} />
                    <BottomNavigationAction label=" " icon={<VpnKeyIcon />} />
                    <BottomNavigationAction label="" icon={<AccountCircleIcon />} />
                </BottomNavigation>
            </Paper>
        </Box>
    );
}
